import { useState, useEffect } from 'react'
import { Text, LabelInput, Title } from '../../components/index'
import * as S from './style'
import Button from '@mui/material/Button'
import Stack from '@mui/material/Stack'
import apiClient from '../../axios'
import { fetchUserInfo } from '../../context/UserContext'
import { useNavigate } from 'react-router-dom'
import SearchResultModal from './SearchResultModal'
import { useAlert } from '../../context/AlertContext'
import { useConfirm } from '../../context/ConfirmContext'

//  !
export default function WishBook() {
  const showConfirm = useConfirm()
  const showAlert = useAlert()
  const navigate = useNavigate()
  const [wishBookName, setWishBookName] = useState('')
  const [wishBookAuthor, setWishBookAuthor] = useState('')
  const [wishBookPublisher, setWishBookPublisher] = useState('')
  const [wishBookISBN, setWishBookISBN] = useState('')
  const [user, setUser] = useState(null)

  const [bookSearchResults, setBookSearchResults] = useState([])
  const [openModal, setOpenModal] = useState(false)
  const [wishBook, setWishBook] = useState({})

  const createWish = (book, user) => {
    const categories = [
      '//',
      '//',
      '',
      '',
      '',
      '',
      '/',
      '/',
      '',
      '',
      '//',
      '/',
      '',
      '',
      '',
      '',
      '/',
      '',
      '',
      '',
      '',
      '',
      '/',
      '/',
      '',
      '',
      '',
      '/',
      'Gift',
    ]

    const bookCategory = categories.includes(book.category)
      ? book.category
      : '/'

    return {
      id: null,
      status: 'Pending',
      email: user.email,
      book: {
        title: book.title,
        author: book.author,
        publisher: book.publisher,
        category: bookCategory,
        info: book.info,
        rent_count: 0,
        isbn: book.isbn,
        cover: book.cover,
        pubDate: book.pubDate,
      },
    }
  }

  useEffect(() => {
    const getUserInfo = async () => {
      const userInfo = await fetchUserInfo()
      setUser(userInfo)
      if (!userInfo) {
        showAlert('  .')
        navigate('/')
      }
      console.log('user : ', user)
    }
    getUserInfo()
  }, [])

  const onClickWishBook = () => {
    if (
      !wishBookName ||
      !wishBookAuthor ||
      !wishBookPublisher ||
      !wishBookISBN
    ) {
      showAlert(', , , ISBN  .')
      return
    }
    showConfirm(' ?', submitWishBook)
  }

  const submitWishBook = () => {
    apiClient
      .post('/api/wish/create', wishBook)
      .then(response => {
        showAlert('  .')
        navigate('/')
      })
      .catch(error => {
        if (error.response.status === 500) {
          showAlert('  .')
        } else {
          showAlert('   .')
        }
        console.error(error) //  
      })
  }

  const callAladinAPI = async wishBookName => {
    try {
      const response = await apiClient.get('/aladin-api/ItemSearch.aspx', {
        params: {
          TTBKey: process.env.REACT_APP_TTB_KEY,
          Query: wishBookName,
          Output: 'JS',
          Cover: 'Big',
        },
      })
      let dataString = response.data
      dataString = dataString.trim() //  
      dataString = dataString.substring(0, dataString.lastIndexOf('}')) + '}' //   

      let dataObject
      try {
        dataObject = JSON.parse(dataString)
      } catch (e) {
        console.error(e)
      }

      console.log('dataObject : ', dataObject)
      const books = dataObject.item.map(book => {
        const categoryParts = book.categoryName.split('>')
        const category =
          categoryParts.length > 1 ? categoryParts[1].trim() : book.categoryName

        return {
          title: book.title,
          author: book.author,
          publisher: book.publisher,
          pubDate: book.pubDate,
          isbn: book.isbn,
          info: book.description,
          cover: book.cover,
          category: category,
        }
      })
      console.log('books ', books)
      return books
    } catch (error) {
      console.error(error)
      return [] //      
    }
  }

  //   
  const handleSelectBook = book => {
    setBookSearchResults([]) //   

    console.log('book : ', book)

    const wish = createWish(book, user)
    setWishBook(wish)
    console.log('wish : ', wish)
    //      
    setWishBookName(book.title)
    setWishBookAuthor(book.author)
    setWishBookPublisher(book.publisher)
    setWishBookISBN(book.isbn)
    setOpenModal(false)
  }
  const handleSearch = async () => {
    if (!wishBookName) {
      showAlert('  .')
      return
    }
    const books = await callAladinAPI(wishBookName)
    setBookSearchResults(books)

    setOpenModal(true)
  }

  return (
    user && (
      <S.InnerContainer>
        <Title text=" " />
        <S.ContentWrap>
          <Stack spacing={3}>
            <S.LabelWrapper>
              <S.LabelTitleWrapper>
                <S.EssentialMark>*</S.EssentialMark>
                <Text
                  text=""
                  color={({ theme }) => theme.colors.grey5}
                  fontSize={({ theme }) => theme.fontSize.sz16}
                />
              </S.LabelTitleWrapper>
              <LabelInput
                type="text"
                placeholder=""
                value={wishBookName}
                onChange={e => setWishBookName(e.target.value)}
                marginTop="0"
                marginRight="50"
                width="300px"
              />
              <Button size="large" variant="contained" onClick={handleSearch}>
                
              </Button>
              <SearchResultModal
                open={openModal}
                books={bookSearchResults}
                onSelectBook={handleSelectBook}
                handleClose={() => setOpenModal(false)}
              />
            </S.LabelWrapper>
            <S.LabelWrapper>
              <S.LabelTitleWrapper>
                <S.EssentialMark>*</S.EssentialMark>
                <Text
                  text=""
                  color={({ theme }) => theme.colors.grey5}
                  fontSize={({ theme }) => theme.fontSize.sz16}
                />
              </S.LabelTitleWrapper>
              <LabelInput
                type="text"
                placeholder=""
                value={wishBookAuthor}
                onChange={e => setWishBookAuthor(e.target.value)}
                marginTop="0"
                width="200px"
              />
            </S.LabelWrapper>
            <S.LabelWrapper>
              <S.LabelTitleWrapper>
                <S.EssentialMark>*</S.EssentialMark>
                <Text
                  text=""
                  color={({ theme }) => theme.colors.grey5}
                  fontSize={({ theme }) => theme.fontSize.sz16}
                />
              </S.LabelTitleWrapper>
              <LabelInput
                type="text"
                placeholder=""
                value={wishBookPublisher}
                onChange={e => setWishBookPublisher(e.target.value)}
                marginTop="0"
                width="200px"
              />
            </S.LabelWrapper>
            <S.LabelWrapper>
              <S.LabelTitleWrapper>
                <S.EssentialMark>*</S.EssentialMark>
                <Text
                  text="ISBN"
                  color={({ theme }) => theme.colors.grey5}
                  fontSize={({ theme }) => theme.fontSize.sz16}
                />
              </S.LabelTitleWrapper>
              <LabelInput
                type="text"
                placeholder=""
                value={wishBookISBN}
                onChange={e => setWishBookISBN(e.target.value)}
                marginTop="0"
                width="200px"
              />
            </S.LabelWrapper>
          </Stack>
        </S.ContentWrap>
        <S.ButtonWrapper>
          <Button size="large" variant="contained" onClick={onClickWishBook}>
            
          </Button>
        </S.ButtonWrapper>
        <div style={{ marginTop: '50px', marginBottom: '50px' }}></div>
      </S.InnerContainer>
    )
  )
}
