import { useNavigate } from 'react-router-dom'
import { LabelInput, Title } from '../../components/index'
import * as S from './style'
import React, { useState, useContext, useEffect } from 'react'
import { useTokenDispatch, IsLoginContext } from '../../context/IsLoginContext'
import { fetchUserInfo } from '../../context/UserContext'
import { SnackbarContext } from '../../context/SnackbarContext'
import apiClient from '../../axios'
import { useAlert } from '../../context/AlertContext'

function Login() {
  const showAlert = useAlert()
  const setToken = useTokenDispatch()
  const { setSnackbar } = useContext(SnackbarContext)
  const { setIsLogin } = useContext(IsLoginContext)
  const [email, setEmail] = useState('')
  const [pw, setPw] = useState('')
  const [emailValid, setEmailValid] = useState(false)
  const [pwValid, setPwValid] = useState(false)
  const [notAllow, setNotAllow] = useState(true)
  const navigate = useNavigate()

  useEffect(() => {
    const getUserInfo = async () => {
      const userInfo = await fetchUserInfo()
      if (userInfo) {
        showAlert('  .')
        navigate('/')
      }
    }
    getUserInfo()
  }, [])

  const handleEmail = e => {
    setEmail(e.target.value)
    const regex =
      /^(([^<>()[\].,;:\s@"]+(\.[^<>()[\].,;:\s@"]+)*)|(".+"))@(([^<>()[\].,;:\s@"]+\.)+[^<>()[\].,;:\s@"]{2,})$/i
    if (regex.test(email)) {
      setEmailValid(true)
    } else {
      setEmailValid(false)
    }
  }

  const handlePassword = e => {
    const value = e.target.value
    setPw(value)
    const regex =
      /^(?=.*[a-zA-z])(?=.*[0-9])(?=.*[$`~!@$!%*#^?&\\(\\)\-_=+])(?!.*[^a-zA-z0-9$`~!@$!%*#^?&\\(\\)\-_=+]).{8,20}$/
    const isValid = value.length >= 8 && regex.test(value)
    setPwValid(isValid)
  }

  const onClickConfirmButton = async () => {
    try {
      const response = await apiClient.post('/api/auth/login', {
        email,
        password: pw,
      })
      console.log('onClickConfirmButton : ', response.data)
      const accessToken = response.data.accessToken
      localStorage.setItem('token', accessToken) // : localStorage  
      apiClient.defaults.headers.common[
        'Authorization'
      ] = `Bearer ${accessToken}` // :  Access Token 
      setToken(accessToken)
      setIsLogin(true)
      setSnackbar({
        open: true,
        severity: 'success',
        message: ' !',
      })

      fetchUserInfo()
      navigate('/')
    } catch (error) {
      setPw('')
      setPwValid(false)
      showAlert('      .')
    }
  }

  const onClickSignUpButton = () => {
    navigate('/signUp')
  }

  useEffect(() => {
    if (emailValid && pwValid) {
      setNotAllow(false)
      return
    }
    setNotAllow(true)
  }, [emailValid, pwValid])

  return (
    <>
      <S.Container>
        <S.InnerContainer>
          <Title text="" />
          <S.ContentWrap>
            <LabelInput
              type="text"
              placeholder=""
              value={email}
              onChange={handleEmail}
              width="100%"
            />
            {!emailValid && email.length > 0 && (
              <S.ErrorMessageWrap>
                <div>  .</div>
              </S.ErrorMessageWrap>
            )}
            <LabelInput
              type="password"
              placeholder=""
              value={pw}
              onChange={handlePassword}
              width="100%"
            />
            {!pwValid && pw.length > 0 && (
              <S.ErrorMessageWrap>
                <div>, ,   8  .</div>
              </S.ErrorMessageWrap>
            )}
          </S.ContentWrap>
          <div style={{ marginTop: '50px' }}>
            <S.LoginButton onClick={onClickConfirmButton} disabled={notAllow}>
              
            </S.LoginButton>
            <S.SignUpButton onClick={onClickSignUpButton}>
              
            </S.SignUpButton>
          </div>
        </S.InnerContainer>
      </S.Container>
    </>
  )
}

export default Login
